import React from "react"
import { StaticQuery, graphql } from "gatsby"

const Card = ({pokemon}) => {
  return (    

    <article className={pokemon.types} data-color={pokemon.types}>
      <div className="title">
        <h3>{pokemon.name}</h3>
        <p>{pokemon.maxHP} HP</p>        
      </div>
      
      <figure>        
        <img        
          src={pokemon.image}
          alt={pokemon.name}
          placeholder="blurred"          
          loading="lazy"
        />
      </figure>      

      <div className="data">
        <p>
          <strong>Peso:</strong> {pokemon.weight.maximum} || 
          <strong> Altura:</strong> {pokemon.height.maximum}
        </p>
      </div>

    </article>
  )
}

const PokeList = () => (
  <StaticQuery
    query={graphql`
    {
      pokemonsdata {
        pokemons(first: 151) {
          id
          name
          maxHP
          maxCP
          image
          resistant
          number
          weaknesses
          weight {
            maximum
            minimum
          }
          height {
            maximum
            minimum
          }
          types
          fleeRate
          attacks {
            special {
              damage
              name
              type
            }
            fast {
              damage
              name
              type
            }
          }
        }
      }
    }
  `}
    render={data => data.pokemonsdata.pokemons.map(pokemon => (
      <Card pokemon={pokemon}></Card>
    ))}
  ></StaticQuery>
)

export default PokeList

